/* src/components/Services.css */

.services {
    min-height: auto;
    padding: 80px 0 60px;
    background-color: #f6e4f7;
}

.services .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.services h2 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 2rem;
    color: #333;
}

.services h3 {
    font-size: 1.875rem;
    text-align: center;
    margin-bottom: 2rem;
    color: #555;
}

.services-grid {
    display: flex;
    justify-content: space-between;
    gap: 30px;
}

.service-item {
    background-color: white;
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex: 1;
    overflow: hidden;
}

.service-item:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.service-icon {
    font-size: 3rem;
    margin-bottom: 15px;
    color: #4a90e2;
}

.service-item h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: #333;
}

.service-item p {
    font-size: 1.125rem;
    line-height: 1.6;
    text-align: center;
    max-width: 80%;
    margin: 0 auto 20px;
    color: #666;
}

.service-item button {
    padding: 10px 20px;
    background-color: #4a90e2;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.service-item button:hover {
    background-color: #357abd;
}

/* Модальное окно */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    max-width: 500px;
    width: 90%;
    position: relative;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    background: none;
    border: none;
    cursor: pointer;
}

/* Градиенты для фона сервисов */
.service-item:nth-child(1) {
    background: linear-gradient(135deg, #f6d365 0%, #fda085 100%);
}

.service-item:nth-child(2) {
    background: linear-gradient(135deg, #84fab0 0%, #8fd3f4 100%);
}

.service-item:nth-child(3) {
    background: linear-gradient(135deg, #a1c4fd 0%, #c2e9fb 100%);
}

/* Медиа-запросы для планшетов */
@media (max-width: 1024px) {
    .services-grid {
        flex-direction: column;
        align-items: center;
    }

    .service-item {
        width: 100%;
        max-width: 500px;
        margin-bottom: 30px;
    }
}

/* Медиа-запросы для телефонов */
@media (max-width: 768px) {
    .services h2 {
        font-size: 2rem;
    }

    .services h3 {
        font-size: 1.5rem;
    }

    .service-item {
        padding: 20px;
    }

    .service-item h3 {
        font-size: 1.25rem;
    }

    .service-item p {
        font-size: 1rem;
    }
}